.links {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 21px;
  font-family: Montserrat;
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  position: relative;
}
.links:first-child {
  margin: 0;
}
.links::after {
  content: "";
  display: block;
  position: absolute;
  top: 55px;
  width: 0;
  left: 0;
  height: 2px;
  background-color: #ff3c00;
  transition: width 0.3s ease-out;
}
.links:hover:after {
  width: 100%;
}

@media screen and (max-width: 1160px) {
  .links {
    margin: 0 5px;
  }
}

@media screen and (max-width: 967px) {
  .links {
    height: 100%;
    border-collapse: collapse;
    border-top: 1px solid #505050;
    border-bottom: 1px solid #505050;
  }
}

@media screen and (max-width: 414px) {
  .links {
    justify-content: flex-start;
    padding-left: 28px;
  }
}

.links-selected::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  height: 2px;
  background-color: #ff3c00;
}
